.spinner {
  width: 5rem;
  height: 5rem;
  border-top-color: #444;
  border-left-color: #444;
  animation: spinner 400ms linear infinite;
  border-bottom-color: transparent;
  border-right-color: transparent;
  border-style: solid;
  border-width: 2px;
  border-radius: 50%;
  box-sizing: border-box;
  display: inline-block;
  margin-left: 150px;
  margin-top: 260px;
}
@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.spinner-large {
  width: 9rem;
  height: 9rem;
  border-width: 6px;
}
.spinner-slow {
  animation: spinner 1s linear infinite;
}
.spinner-blue {
  border-top-color: #09d;
  border-left-color: #09d;
}
.bolt {
  position: absolute;
  margin-left: 190px;
  margin-top: -94px;
  font-size: 25px;
  font-weight: 500;
  color: grey;
}
.bolt-span-right {
  color: #09d;
}
