.user-input {
  display: inline;
  width: calc(50% - 50px);
  height: 40px;
  background-color: transparent;
  border: 1px solid #09d;
  outline: none;
  border-radius: 50px;
  padding: 0 20px 0 20px;
  opacity: 0.7;
  font-size: 18px;
  color: rgb(80, 80, 80);
}

.user-input:nth-child(2) {
  margin-left: 10px;
}

.user-box {
  display: block;
  display: block;
  width: calc(100% - 80px);
  padding-left: 5px;
  margin: 0 auto 0 auto;
}
.heading:hover {
  cursor: context-menu;
}
