.message-box {
  position: relative;
  display: block;
  width: 450px;
  height: 670px;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 0 50px 10px rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

.message-header {
  margin: 40px 0 0 60px;
  width: calc(100% - 110px);
  height: 80px;
  overflow: hidden;
}
.image-box {
  display: inline-block;
  width: 80px;
  height: 80px;
  border-radius: 40px;
  background-color: rgb(245, 245, 245);
}

.data-box {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 100px);
  height: 80px;
  margin-left: 20px;
}
.message-title {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 20px);
  height: 30px;
  border-radius: 40px;
  padding-left: 20px;
  padding-top: 7px;
  font-weight: 600;
  font-size: 18px;
  color: #09d;
  background-color: rgb(245, 245, 245);
}

.message-details {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 100px);
  height: 22px;
  margin-top: 10px;
  border-radius: 40px;
  padding-left: 20px;
  font-weight: 600;
  font-size: 14px;
  color: gray;
  padding-top: 3px;
  background-color: rgb(245, 245, 245);
}

.message-line {
  display: block;
  width: calc(100% - 60px);
  height: 1px;
  margin: 20px auto 0 auto;
  background-color: #09d;
}

.message-body {
  display: block;
  width: calc(100% - 40px);
  height: 400px;
  margin: 20px auto 0 auto;
  border-radius: 15px;
  overflow-x: hidden;
  overflow-y: auto;
}

.message-send {
  margin: 20px auto 0 auto;
  width: calc(100% - 40px);
  height: 40px;
  overflow: hidden;
}

.message-input {
  display: inline-block;
  width: calc(100% - 130px);
  height: 40px;
  background-color: rgb(245, 245, 245);
  border: none;
  outline: none;
  border-radius: 50px;
  padding: 0 20px 0 20px;
  font-size: 18px;
  color: rgb(80, 80, 80);
}

.message-button {
  display: inline-block;
  width: 80px;
  border: 1px solid #09d;
  height: 38px;
  margin-left: 10px;
  color: white;
  outline: none;
  border-radius: 50px;
  font-size: 16px;
  background-color: #09d;
  transition: color 0.1s, background-color 0.1s;
  cursor: pointer;
}

.message-button:hover {
  background-color: white;
  color: #09d;
}

.message-left {
  position: relative;
  display: block;
  width: calc(100% - 20px);
  overflow: hidden;
  margin: 10px 0 0 20px;
}

.message-left span {
  position: relative;
  float: left;
  max-width: calc(50% - 40px);
  display: block;
  padding: 10px 20px 10px 20px;
  border-radius: 20px 20px 20px 0;
  color: white;
  background-color: rgb(80, 80, 80);
  overflow-x: hidden;
}

.message-right {
  position: relative;
  display: block;
  width: calc(100% - 20px);
  overflow: hidden;
  margin: 10px 0 0 20px;
}

.message-right span {
  position: relative;
  float: right;
  max-width: calc(50% - 40px);
  display: block;
  padding: 10px 20px 10px 20px;
  border-radius: 20px 0 20px 20px;
  color: rgb(80, 80, 80);
  margin-right: 10px;
  background-color: rgb(230, 230, 230);
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: rgb(200, 200, 200);
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(180, 180, 180);
}

.test {
  background-color: red;
}
.back-button-message-box {
  position: absolute;
  margin-left: 15px;
  margin-top: 20px;
  font-size: 25px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  color: rgb(80, 80, 80);
}
