.profile-pic-profile {
  /*Display: inline-block was removed*/
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background-color: rgb(245, 245, 245);
  margin-top: 75px;
  margin-left: auto;
  margin-right: auto;
}
.profile-id-profile {
  text-align: center;
  margin-bottom: 20px;
}
.profile-name-profile {
  text-align: center;
  margin-top: 15px;
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 5px;
  color: #09d;
}
.profile-title-profile {
  width: 90%;
  margin-left: 5%;
  text-align: center;
  font-size: 16px;
  border: none;
  outline: none;
}
.profile-email-profile {
  color: black;
  font-size: 18px;
  text-align: center;
  margin-top: 20px;
}

.profile-email-profile span {
  opacity: 0.7;
}
.profile-bio-profile {
  width: calc(50% - 0px);
  height: 155px;
  font-size: 16px;
  border: none;
  border-radius: 20px;
  margin-top: 17px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  resize: none;
  outline: rgb(219, 215, 215);
  padding: 10px 20px 20px 10px;
  font-family: "Segoe UI";
}
.profile-bio-profile::placeholder {
  text-align: center;
}
.message-line-after-title {
  display: block;
  width: calc(100% - 130px);
  height: 1px;
  margin: 23px auto 0 auto;
  background-color: #09d;
}
.message-line-after-email {
  display: block;
  width: calc(100% - 130px);
  height: 1px;
  margin: 20px auto -10px auto;
  background-color: #09d;
}
.profile-submit-button {
  display: block;
  margin: 20px 40px 0 auto;
  width: calc(100% - 350px);
  height: 43px;
  background-color: #09d;
  border: 1px solid #09d;
  outline: none;
  border-radius: 50px;
  padding: 0 20px 0 20px;
  font-size: 18px;
  cursor: pointer;
  color: white;
}
.profile-submit-button:hover {
  background-color: white;
  color: black;
  transition: color 0.1s, background-color 0.1s;
}
.profile-section-back-button {
  position: absolute;
  margin-left: 15px;
  margin-top: 20px;
  font-size: 25px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
  color: rgb(80, 80, 80);
}
