.heading {
  text-align: center;
  font-size: 32px;
  margin: 60px 40px 60px 40px;
  font-weight: 600;
  color: black;
}
.main-input {
  display: block;
  margin: 30px auto 0 auto;
  width: calc(100% - 130px);
  height: 40px;
  background-color: transparent;
  border: 1px solid #09d;
  outline: none;
  opacity: 0.7;
  border-radius: 50px;
  padding: 0 20px 0 20px;
  font-size: 18px;
  color: rgb(80, 80, 80);
}
.main-button {
  display: block;
  margin: 30px auto 0 auto;
  width: calc(100% - 84px);
  height: 43px;
  background-color: #09d;
  border: 1px solid #09d;
  outline: none;
  border-radius: 50px;
  padding: 0 20px 0 20px;
  font-size: 18px;
  cursor: pointer;
  color: white;
  transition: color 0.1s, background-color 0.1s;
}

.main-button:hover {
  background-color: transparent;
  color: black;
}

.main-button:disabled {
  opacity: 0.8;
  cursor: context-menu;
}

.main-button:disabled:hover {
  background-color: #09d;
  color: white;
}

.forgot {
  display: block;
  color: rgb(80, 80, 80);
  margin: 30px 0 30px 50px;
  text-decoration: none;
}

.details {
  text-align: center;
  margin: 30px 0 30px 0;
  font-size: 18px;
  color: rgb(80, 80, 80);
}

.notice {
  display: block;
  margin: 30px 0 30px 50px;
  font-size: 18px;
  color: red;
}

.details button {
  background-color: transparent;
  border: 0;
  font-size: 18px;
  text-decoration: none;
  color: #4251f5;
  cursor: pointer;
}
